import React from 'react';



function Card(props){
  return (
    <>
      <div className='card'>
        <div className='card-img'>
          <img src={props.imgsrc} alt="money-heist"/>
        </div>
        <div className="card-title">
        {props.title}
        </div>
        <a href={props.link} target="_blank">
        <input className='but' type="button" value="Watch Now"/>
        </a>
      </div>
     </>
  );
}

export default Card;